type Props = {
    choosenMsg: any
    setChoosenMsg: (v: any[]) => void
    setAction: (v: any) => void
    setIsChoose: (v: boolean) => void
    deleteMessage: (ids: string | string[]) => Promise<void>
    refresh: () => Promise<void>
}
export function useDeleteMessage(props: Props) {
    const { setChoosenMsg, setAction, setIsChoose, choosenMsg, deleteMessage, refresh } = props

    const handleDeleteMessage = async (id: string) => {
        try {
            if (!id) {
                console.warn("No message ID provided")
                return
            }
            await deleteMessage(id)
            await refresh()
            setAction(null)
        } catch (error) {
            console.error("Error deleting message:", error)
        }
    }

    const handleDeleteManyMsg = async () => {
        try {
            await deleteMessage(choosenMsg)
            await refresh()
            setChoosenMsg([])
            setIsChoose(false)
        } catch (error) {
            console.error("Error deleting message:", error)
        }
    }

    return{handleDeleteMessage, handleDeleteManyMsg}
}

